import XButton from 'vux/src/components/x-button/index.vue'


export default {

name: 'app-person-scan',

components: {
XButton,
},

data() {
return {
text: '',

};
},

methods: {

getQueryString(url, parmas) {

return decodeURIComponent((new RegExp(`[?|&]${parmas}=` + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
},


scanQRCode() {
console.log('');

this.$wechat.scanQRCode({
needResult: 1, // 01
scanType: ['qrCode', 'barCode'], // 
success(res) {
this.text = 'id';

const result = res.resultStr;



const id = decodeURIComponent((new RegExp('[?|&]wash_record_id=' + '([^&;]+?)(&|#|;|$)').exec(result) || [, ''])[1].replace(/\+/g, '%20')) || null;

console.log(`wash_record_id:${id}`);


if (id) {
this.text = '';
console.log('');
this.start(id);
} else {
this.text = 'wash_record_id';
console.log('wash_record_id');

}
},
});
},


start(id) {
this.$http.post('mobileMachine!checkNumber.do', {
machine_number: id,
})
.then(() => {
this.$vux.toast.text('', 'top');
this.text = '';
})
.catch((error) => {
if (+error.State === 3) {
this.$vux.toast.text(error.Message, 'top');
setTimeout(() => {
location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx4344aedbc2111bb0&redirect_uri=http%3a%2f%2fapp.yunlinggroup.com%2fcxcar%2f%23%2fpay&response_type=code&scope=snsapi_base&state=123#wechat_redirect';
}, 2000);
} else {
this.$vux.toast.text(error, 'top');
}
});
},


back() {
history.back(-1);
},

},

created() {
this.scanQRCode();

this.start('600845');
},
};




