import Countup from 'vux/src/components/countup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {

name: 'app-person-index',

data() {
return {
uname: '', // 
balance: 0, // 
};
},

components: {
Countup,
XButton,
Flexbox,
FlexboxItem,
},

methods: {

getMemberDetail() {
this.$http.get('/mobileMember!getMemberDetail.do')
.then((res) => {
this.uname = res.uname;
this.balance = res.balance;
})
.catch((error) => {
this.$vux.toast.text(error, 'top');
});
},

goTo() {
location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx4344aedbc2111bb0&redirect_uri=http%3a%2f%2fapp.yunlinggroup.com%2fcxcar%2f%23%2fpay&response_type=code&scope=snsapi_base&state=123#wechat_redirect';
},
},

created() {
this.getMemberDetail();
},

};




