import { mapState } from 'vuex';

import { GLOBAL } from '@/store/mutationTypes';
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {

name: 'app-person-login',

components: {
CheckIcon,
XInput,
XButton,
Group,
},

data() {
return {
form: {
uname: '', // 
validate_code: '', // 
},

active: {
agree: true, // 
submit: false, // 
},

vcodeActive: false, // 
vcodeText: '',
vcodeTime: 60,
};
},

computed: {
...mapState(['loading']),

isTel() {
const reg = /^1\d{10}$/; // 
return reg.test(this.form.uname);
},
isVcode() {
const reg = /^\d{6}$/; // 6
return reg.test(this.form.validate_code);
},
isAgree() {
return this.active.agree;
},
},

methods: {

timeReduce() {
if (this.vcodeTime === 0) {
this.vcodeActive = false;
this.vcodeText = '';
this.vcodeTime = 60;
} else {
this.vcodeActive = true;
this.vcodeText = `${this.vcodeTime} S`;
this.vcodeTime = this.vcodeTime - 1;
setTimeout(() => {
this.timeReduce();
}, 1000);
}
},


sendValidateCode() {
if (this.isTel) {
this.$http.post('/mobileMember!sendValidateCode.do', {
uname: this.form.uname,
})
.then(() => {
this.$vux.toast.text('', 'top');
})
.catch((error) => {
this.$vux.toast.text(error, 'top');
});

this.timeReduce();
} else {
this.$vux.toast.text('', 'top');
}
},


beforeSubmit() {
if (this.isTel === false) {
this.$vux.toast.text('', 'top');
} else if (this.isVcode === false) {
this.$vux.toast.text('6', 'top');
} else if (this.isAgree === false) {
this.$vux.toast.text('', 'top');
} else {
this.submit();
}
},


submit() {
this.active.submit = true;

this.$http.post('/mobileMember!login.do', {
uname: this.form.uname,
validate_code: this.form.validate_code,
})
.then((res) => {
this.active.submit = false;

localStorage.setItem('token', res.token);
localStorage.setItem('member_id', res.member_id);

this.$store.commit(GLOBAL.TOKEN.UPDATE, res.token);
this.$store.commit(GLOBAL.MEMBERID.UPDATE, res.member_id);
this.$store.commit(GLOBAL.UNAME.UPDATE, res.uname);
this.$router.push('/person');
})
.catch((error) => {
this.active.submit = false;
this.$vux.toast.text(error, 'top');
});
},
},

created() {

},

};



