

import ViewBox from 'vux/src/components/view-box/index.vue'















export default {
name: 'app',

components: {
ViewBox,
},

data() {
return {
appId: '',
timestamp: '',
nonceStr: '',
signature: '',
jsapi_ticket: '',


jsApiList: [
'checkJsApi',
'openLocation',
'getLocation',
'scanQRCode',
'chooseWXPay',
],
};
},

methods: {

getJssdk() {
this.$http.post('/mobileAppPay!getJssdk.do', {
url: window.location.href.split('#')[0],

})
.then(({ receiveMsg }) => {
this.appId = receiveMsg.appId;
this.timestamp = receiveMsg.timestamp;
this.nonceStr = receiveMsg.nonceStr;
this.signature = receiveMsg.signature;
this.jsapi_ticket = receiveMsg.jsapi_ticket;
this.wxConfig();
})
.catch((error) => {
this.$vux.toast.text(error, 'top');
});
},


wxConfig() {
this.$wechat.config({
debug: true,

appId: this.appId, // 
timestamp: this.timestamp, // 
nonceStr: this.nonceStr, // 
signature: this.signature, // 
jsApiList: this.jsApiList, // JS
success() {
this.checkJsApi();
},
});
},


checkJsApi() {
this.$wechat.checkJsApi({
jsApiList: this.jsApiList, // JS
success(res) {
console.log(res);


},
});
},
},

created() {


this.getJssdk();
},
};




