*/

import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Search from 'vux/src/components/search/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {

name: 'app-person-List',

components: {
XButton,
XHeader,
Flexbox,
FlexboxItem,
XInput,
Search,
LoadMore,
},

data() {
return {
searchText: '', // 
car_list: [], // 

addr_lng: 118.1, // 
addr_lat: 24.46, // 
};
},

watch: {
/**
* 
* 
* 
*/
searchText(val) {
if (val === '') {
this.getMachineList();
}
},
},

methods: {


openLocation(lat, lng, name, address) {
console.log(`${lat};${lng};${name};${address};`);

this.$wechat.openLocation({
latitude: lat,
longitude: lng,
name,
address,
scale: 14,
infoUrl: 'http://weixin.qq.com',
});
},

goTo() {
this.$router.push('/person');
},


getMachineList() {
console.log(`${this.addr_lng},${this.addr_lat}`);
this.$http.post('/mobileMachine!getMachineList.do', {
addr_lng: this.addr_lng,
addr_lat: this.addr_lat,
search_address: this.searchText,
city_name: '',
})
.then((res) => {
this.car_list = res.car_list;
})
.catch((error) => {
this.$vux.toast.text(error, 'top');
});
},


getLocation() {
this.$wechat.getLocation({
type: 'wgs84', // wgs84gpsopenLocation'gcj02'
success(res) {
console.log(`jssdk${res.longitude};${res.latitude}`);
this.addr_lng = res.longitude;// 180 ~ -180
this.addr_lat = res.latitude; // 90 ~ -90
},
});
},
},

created() {
this.getLocation();
this.getMachineList();
},
};




