import Vue from 'vue';
import Vuex from 'vuex';

import person from './person';
import pay from './pay';
import { GLOBAL } from './mutationTypes';

Vue.use(Vuex);

const store = new Vuex.Store({
plugins: [],

modules: {
person,
pay,
},

getters: {},

state: {
token: '', // 
member_id: '', // id
uname: '', // 
loading: false, // 
},

/* eslint-disable no-param-reassign */
mutations: {
[GLOBAL.TOKEN.UPDATE](state, token) { // token
state.token = token;
},

[GLOBAL.MEMBERID.UPDATE](state, memberid) { // member_id
state.member_id = memberid;
},

[GLOBAL.UNAME.UPDATE](state, uname) { // uname
state.uname = uname;
},

[GLOBAL.LOADING.SHOW](state) { // 
state.loading = true;
},

[GLOBAL.LOADING.HIDE](state) { // 
state.loading = false;
},
},
/* eslint-enalbe */

actions: {},

});

export default store;



