
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'


export default {
data() {
return {

pullupConfig: {
content: '',
height: 40,
downContent: '',
upContent: '',
loadingContent: '...',
},

pulldownConfig: {
content: '',
height: 60,
downContent: '',
upContent: '',
loadingContent: '...',
},

status: {

pullupStatus: 'default',
pulldownStatus: 'default',
},

n: 20,

};
},

components: {
Group,
Cell,
Scroller,
Spinner,
},

methods: {

loadMore1() {
setTimeout(() => {
this.n += 10;
setTimeout(() => {
this.$refs.demo.donePullup();
}, 100);
if (this.n === 50) {
setTimeout(() => {

this.$refs.demo.disablePullup();
}, 100);
}
}, 2000);
},


loadMore2() {
setTimeout(() => {
this.n += 10;
setTimeout(() => {
this.status.pullupStatus = 'default';
}, 100);
if (this.n === 50) {
setTimeout(() => {

this.status.pullupStatus = 'disabled';
}, 100);
}
}, 2000);
},


refresh1() {
setTimeout(() => {
this.n = 20;
this.$nextTick(() => {
setTimeout(() => {
this.$refs.demo.donePulldown();
this.$refs.demo.enablePullup();
}, 10);
});
}, 2000);
},


refresh2() {
setTimeout(() => {
this.n = 20;
this.$nextTick(() => {
setTimeout(() => {
this.status.pulldownStatus = 'default';

this.status.pullupStatus = 'enabled';
}, 10);
});
}, 2000);
},
},

};



