import * as views from '@/views';

export default [
  {
    path: '/coupon',
    name: 'Coupon',
    component: views.Coupon,
    children: [
      {
        path: '/coupon/get',
        name: 'CouponGet',
        component: views.CouponGet,
      },
      {
        path: '/coupon/get/:id',
        name: 'CouponDetail',
        component: views.CouponDetail,
        meta: {
          title: '',
        },
      },
    ],
  },

];



