import * as views from '@/views';

export default [
  {
    path: '/pay',
    name: 'Pay',
    component: views.Pay,
    meta: {
      title: '',
      needToken: true,
    },
  },
  {
    path: '/pay/ok',
    name: 'PayOk',
    component: views.PayOk,
    meta: {
      title: '',
      needToken: true,
    },
  },
  {
    path: '/pay/history',
    name: 'PayHistory',
    component: views.PayHistory,
    meta: {
      title: '',
      needToken: true,
    },
  },
];



