import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {

name: 'app-coupon-get',

components: {
Group,
XInput,
XButton,
},

data() {
return {
moible: '',
text: '',
loading: false,
};
},

computed: {
isTel() {
const reg = /^1\d{10}$/; // 
return reg.test(this.moible);
},
},

methods: {
beforeSubmit() {
if (this.isTel === false) {
this.$vux.toast.text('', 'top');
} else {
this.submit();
}
},

submit() {
this.loading = true;
this.text = '...';
this.$router.push('/coupon/get/13003987042');
},
},

created() {
},

};



