import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {

name: 'app-pay-index',

data() {
return {
charge_list: [], // 
id: null, // id
loading: false, // loading
loadingText: '',

appId: '', // appId
timeStamp: '', // 
nonceStr: '', // 
package: '', // prepay_idprepay_id=***
paySign: '', // 
};
},

components: {
XButton,
Flexbox,
FlexboxItem,
},

computed: {
code() {

return decodeURIComponent((new RegExp('[?|&]code=' + '([^&;]+?)(&|#|;|$)').exec(window.location.href) || [, ''])[1].replace(/\+/g, '%20')) || null;
},
},

methods: {

choseRechargePrice(price, id) {
this.id = id;
this.$vux.toast.text(`${price}`, 'middle');
},


beforeWxRecharge() {
if (this.id) {
this.getWxpayParam();
} else {
this.$vux.toast.text('', 'top');
}
},


getWxpayParam() {
this.loading = true;
this.loadingText = '...';

this.$http.post('/mobileAppPay!getMppayParam.do', {
type: 1,
sign_id: this.id,
code: this.code,
})
.then(({ pay_message }) => {
console.log(+pay_message.mch_id);
this.loading = false;
this.loadingText = '';

this.appId = pay_message.appid;
this.timeStamp = +pay_message.mch_id;
this.nonceStr = pay_message.nonce_str;
this.package = pay_message.package;
this.paySign = pay_message.sign;

this.prepayid = pay_message.prepay_id;

this.chooseWXPay();
})
.catch((error) => {
this.loading = false;
this.loadingText = '';
this.$vux.toast.text(error, 'top');
});
},


chooseWXPay() {
this.$wechat.chooseWXPay({
appId: this.appId,

timeStamp: this.timeStamp,
nonceStr: this.nonceStr, //  32 
package: `prepay_id=${this.prepayid}`, // prepay_idprepay_id=***
signType: 'MD5', // 'SHA1''MD5'
paySign: this.paySign, // 

success(res) {
console.log(res);
this.$router.push('/pay/ok');
},
});
},


getRechargeList() {
this.$http.get('/mobileEntiretySystem!getRechargeList.do')
.then((res) => {
this.charge_list = res.charge_list;
})
.catch((error) => {
this.$vux.toast.text(error, 'top');
});
},
},

created() {
this.getRechargeList();
},

};



