import XButton from 'vux/src/components/x-button/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


export default {

name: 'app-error',

components: {
XButton,
Icon,
},

data() {
return {
statusData: [
{
code: 500,
text: '',
},
{
code: 403,
text: '',
},
{
code: 404,
text: 'Not Found !!!',
},
{
code: 405,
text: '',
},
],
};
},

computed: {
status() {
return +this.$router.currentRoute.params.status;
},
},

created() {

},

};



